html, body, #app, #app>div {
  height: 100%
}

.App {
  text-align: center;
}

#particle-js {
  position:fixed !important; 
  height: 100vh;
  width: 100%;
  z-index: -100;
  top: 0;
  left: 0;
}

@media only screen and (max-width: 376px) {
  .HeaderTextLgB {
    font-family: DMSans-Bold;
    font-size: 3em;
    opacity: 0; 
    animation: fadeInUp 1s ease-in-out 0s forwards;
    margin: 0px !important; 
  }

  .HeaderTextMB {
    font-family: DMSans-Bold;
    font-size: 2em; 
    opacity: 0;
    animation: fadeInDown 1s ease-in-out 0s forwards;
    animation-delay: 1s; 
    margin: 0px !important;
  }

  .MoviePageHeader {
    font-family: DMSans-Bold;
    font-size: 2.5em;
    opacity: 0; 
    animation: fadeInUp 1s ease-in-out 0s forwards;
    margin: 0px !important; 
    padding-right: 30px;
    padding-top: 15px;
  }

  .MoviePageSubHeader {
    font-family: DMSans-Bold;
    font-size: 1.5em; 
    opacity: 0;
    animation: fadeInDown 1s ease-in-out 0s forwards;
    animation-delay: 1s; 
    margin: 0px !important;
    padding-right: 30px;
  }

  .ButtonText {
    font-family: DMSans-Medium;
    font-size: 1.25em; 
  }

  .MovieButton {
    font-family: DMSans-Medium; 
    font-size: 1.5em; 
  }
}

@media only screen and (min-width: 378px) {
  .HeaderTextLgB {
    font-family: DMSans-Bold;
    font-size: 4em;
    opacity: 0; 
    animation: fadeInUp 1s ease-in-out 0s forwards;
    margin: 0px !important; 
  }

  .HeaderTextMB {
    font-family: DMSans-Bold;
    font-size: 2.5em; 
    opacity: 0;
    animation: fadeInDown 1s ease-in-out 0s forwards;
    animation-delay: 1s; 
    margin: 0px !important;
  }

  .MoviePageHeader {
    font-family: DMSans-Bold;
    font-size: 2.75em;
    opacity: 0; 
    animation: fadeInUp 1s ease-in-out 0s forwards;
    margin: 0px !important; 
    padding-right: 30px;
    padding-top: 15px;
  }

  .MoviePageSubHeader {
    font-family: DMSans-Bold;
    font-size: 1.5em; 
    opacity: 0;
    animation: fadeInDown 1s ease-in-out 0s forwards;
    animation-delay: 1s; 
    margin: 0px !important;
    padding-right: 30px;
  }

  .ButtonText {
    font-family: DMSans-Medium;
    font-size: 1.5em; 
  }
  .MovieButton {
    font-family: DMSans-Medium; 
    font-size: 2em; 
  }
}

@media only screen and (min-width: 426px) {
  .MoviePageHeader {
    font-family: DMSans-Bold;
    font-size: 4em;
    opacity: 0; 
    animation: fadeInUp 1s ease-in-out 0s forwards;
    margin: 0px !important; 
  }

  .MoviePageSubHeader {
    font-family: DMSans-Bold;
    font-size: 2.5em; 
    opacity: 0;
    animation: fadeInDown 1s ease-in-out 0s forwards;
    animation-delay: 1s; 
    margin: 0px !important;
  }

  .MovieButton {
    font-family: DMSans-Medium; 
    font-size: 3em; 
  }
}





.ContainerStyle {
  margin: auto;
  height: 100vh; 
}

.ScrollableContainer {
  margin-top: 30px; 
  height: 100vh; 
  overflow-y: scroll; 
}

.MarginTop {
  margin-top: 10em !important; 
}

.SearchBarStyle {
  margin-top: 3em; 
}

.IconStyle {
  font-size: 50px;
}



.MetaText {
  font-family: DMSans-Medium;
  font-size: 1.25em; 
}

.DescriptText {
  font-family: DMSans-Medium;
  font-size: 1.5em; 
}

.ActorsText {
  font-family: DMSans-Medium;
  font-size: 1.25em
}

.BackButton {
  float: left; 
  padding-top: 30px; 
}

.ui.modal,
.ui.active.modal {
  margin: 0 auto!important;
  top: 20% !important;
  left: auto !important;
  transform-origin: center !important;
  transition: all ease .5s;
}












@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1; 
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0,-100%,0);
    transform: translate3d(0,100%,0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none; 
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0,100%,0);
    transform: translate3d(0,-80%,0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none; 
  }
}
